#person-details {
  position: relative;
  display: flex;
  flex-direction: column;
}
#person-details .person-details-header {
  width: 100%;
  height: 100vh;
  position: relative;
}
#person-details .person-details-header-image {
  width: 100%;
  height: 100vh;
}
#person-details .person-details-header-image::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, transparent 0, #000);
}
#person-details .person-details-header-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.65;
}

#person-details .person-details-header-content {
  position: absolute;
  bottom: 0;
  z-index: 999;
  display: flex;
  color: #fff;
  font-size: 2rem;
  padding: 10rem;
}

#person-details
  .person-details-header-content
  .person-details-header-content-picture
  img {
  width: 15rem;
  height: 20rem;
  object-fit: cover;
  border: 2px solid #333;
  margin-right: 3rem;
}

#person-details
  .person-details-header-content
  .person-details-header-content-details {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  margin-bottom: 1rem;
}
#person-details
  .person-details-header-content
  .person-details-header-content-details
  .person-details-header-content-details-title {
  font-size: 3rem;
  font-weight: 500;
}

#person-details .person-details-content {
  padding: 10rem 0;
  color: #fff;
  display: flex;
  flex-direction: column;
}

/* BIOGRAPHY  */
#person-details .person-details-content .person-details-content-biography {
